import Request from './request'

const member = new Request()
member.setConfig((config) => { /*  */
  config.baseUrl = 'https://fc.dhkzw.top/api/'
  config.header = {
    ...config.header
  }
  // config.custom = { auth: true }
  return config
})

member.interceptor.request((config, cancel) => { /*  */
  config.header = {
    ...config.header,
		token: uni.getStorageSync("tokens") ? uni.getStorageSync("tokens") : ''
  }
  // if (config.custom.auth) {
  //   config.header.token = 'token'
  // }
  /*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
  return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
member.validateStatus = (statusCode) => {
  return statusCode === 200
}

member.interceptor.response((response) => { /*  */
  if (response.data.code == 201 || response.data.code == 202 || response.data.code == 203) { // 202token)token
	  uni.removeStorage({
			key:'memberInfo',
			success:function() {
				// console.log(' ')-----key 
			}
		})
		uni.removeStorage({
			key:'tokens',
			success:function() {
				// console.log(' ')-----key 
			}
		})
	  uni.redirectTo({ url: "../../pages/dealerLogin/dealerLogin" });
  }
  return response
}, (response) => { // 
  return response
})

const http = new Request()

http.setConfig((config) => { /*  */
  config.baseUrl = 'https://fc.dhkzw.top/api/' /*  */
  config.header = {
    ...config.header,
    token: 1
  }
  return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
http.validateStatus = (statusCode) => {
  return statusCode === 200
}

http.interceptor.request((config, cancel) => { /*  */
  config.header = {
    ...config.header,
		token: uni.getStorageSync("token") ? uni.getStorageSync("token") : ''
  }
  return config
})

http.interceptor.response((response) => { /*  */
  // if (response.data.code !== 200) { // 200reject()
  //   return Promise.reject(response)
  // }
  if (response.data.code == 201 || response.data.code == 202 || response.data.code == 203) { // 202token)token
	  uni.removeStorage({
	  	key:'userInfo',
	  	success:function() {
	  		// console.log(' ')-----key 
	  	}
	  })
		uni.removeStorage({
			key:'token',
			success:function() {
				// console.log(' ')-----key 
			}
		})
	  uni.redirectTo({ url: "../../pages/dealerLogin/dealerLogin" });
  }
  // if (response.config.custom.verification) { // 
  //   return response.data
  // }
  return response
}, (response) => { // 
  return response
})

export {
  http,
  member
}
